//   :   (. , @)    
export function checkUserEmail(email) {
  return /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/.test(email)
}

//   :   , x    , 8
export function checkPassword(password) {
  return /^(?=.*[a-zA-z])(?=.*\d)(?=.*[@$!%*#?&])[a-zA-z\d@$!%*#?&]{8,}$/.test(password)
}
